body{
  /* position: relative; */
  background-image: linear-gradient(
    to right,
    hsl(211, 100%, 50%),
    hsl(179, 100%, 30%)
  );
  z-index: 1;
  margin: 2px;
}

.login_box{
    position: absolute;
    border: 1px solid blue;
    top: 50%;
    left: 50%;
    width: 300px;
    height: 300px;
    border-radius: 5px;
    box-shadow: 0px 0px 8px 2px #ccc;
    border: 1px solid #d2d2d3;
    margin-top: -114px;
    margin-left: -150px;
    text-align: center;
}

.row{
    display:flex;
    flex-direction:column;
    align-items:center;
    padding-top:2rem;
    max-width:100%;
}

.login-input{
    padding: 10px 0px 10px 10px;
    width: 80%;
    border: 2px solid #fff;
    border-radius: 4px;
    outline: none;
}

.login-btn{
    width: 105px;
    height: 40px;
    border-radius: 9px;
    color: #fff;
    background-color: crimson;
    font-family: sans-serif;
    font-weight: 800;
    box-shadow: 0px 0px 3px 2px #a14343;
}

.login-title{
    font-size:40px;
    background: -webkit-linear-gradient(#f12f2f, #2b0566);;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
#error-block{
    
}
.fadeIn{
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    height: 58px;
    background-color: #e51374;
    color: #fff;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    box-sizing: border-box;
    padding: 15px;
    opacity:1;
    transition: width 0.5s 0.5s, height 0.5s, opacity 0.5s 0.5s;
}
.fadeOut{
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    height: 58px;
    background-color: #e51374;
    color: #fff;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    box-sizing: border-box;
    padding: 15px;
    opacity: 0;
    transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}