.topnav {
    overflow: hidden;
    background-color: #333;
  }
  
  .topnav li {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    list-style: none;
  }
  
  .topnav li:hover {
    background-color: #04aa6d;
    color: black;
  }
  .topnav li.right {float: right;}
  
  .topnav li.active {
    background-color: #04AA6D;
    color: white;
  }
  
.link-style{
  color: #fff !important;
  text-decoration: none;
}